.first-page {
  position: relative;
  height: 100vh;
  @media (min-width: 1500px) {
    height: 1056px;
  }
  @include sm {
    .music-player {
      display: none;
    }
  }
  lottie-player {
    width: 180px;
    height: auto;
    position: absolute;
    right: -200px;
    top: -100px;
    @include sm {
      display: none;
    }
  }
  &__bg {
    height: 100%;
    width: 100%;
    //object-fit: cover;
    position: absolute;
    inset: 0;
    @media (max-width: 1300px) {
      object-fit: cover;
    }
    @media (min-width: 1500px) {
      object-fit: fill;
    }
  }

  &__anima {
    object-fit: cover;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 10;
    width: 215px;
    height: 215px;
  }

  &__logo {
    position: absolute;
    top: 45px;
    z-index: 10;
    left: 50%;
    transform: translate(-50%,0);
    max-width: 410px;
    @include xs {
      width: 275px;
      top: 85px;
    }
  }

  .container  {
    height: 100%;
  }

  &-content {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  &__text {
    font-weight: 400;
    font-size: 48px;
    line-height: 140%;
    text-transform: uppercase;
    color: #FFFFFF;
    font-family: Ukraine,sans-serif;
    z-index: 10;
    top: 40%;
    position: absolute;
    display: flex;
    justify-content: flex-end;
    @include sm {
      max-width: 410px;
      width: 100%;
      text-align: center;
      top: 33%;
    }
    @include xs {
      font-size: 24px;
      max-width: 230px;
    }
    .anima {
      transform: translateX(-200px) translateY(60px);
    }
  }

  &__join {
    z-index: 10;
    bottom: 30%;
    position: absolute;
    p {
      font-weight: 500;
      font-size: 18px;
      //line-height: 115px;
      text-transform: uppercase;
      color: #F9B235;
      z-index: 11;
      position: relative;
      @include xs {
        font-size: 14px;
        line-height: 115px;
      }
    }
    .join-block {
      position: relative;
    }

    .join-circle {
      position: absolute;
      transform: translate(-50%,-50%);
      left: 50%;
      top: 50%;
      width: 140px;
      height: 140px;
      border: 1.5px solid #FFFFFF;
      border-radius: 50%;
      transition: border 0.3s;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid #FFFFFF;
      box-shadow: inset 0px 0px 45px rgba(255, 255, 255, 0.25);
      backdrop-filter: blur(8px);
      @include xs {
        width: 90px;
        height: 90px;
      }
      &.active  {
        border: 0px;
        transition: border 0.3s;
        backdrop-filter: blur(0px);
        box-shadow:none;
      }
      &:hover {
        cursor: pointer;
      }
    }

    .join-anima {
      position: absolute;
      transform: translate(-50%,-50%);
      left: 50%;
      top: 50%;
      width: 140px;
      height: 140px;
      border-radius: 50%;
      opacity: 0;
      transition: opacity 0.3s;
      z-index: -1;
      &.active {
        opacity: 1;
        transition: opacity 0.3s;
      }
    }
  }
}

.anima {
  position: relative;
}
.spark {
  background-color: #DE4A00;
  font-family: 'Helvetica', sans-serif;
  visibility: hidden;
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 30%;
  box-shadow: 0 0 5px #AB000B;
}

.first-page-popup {
  @supports(-webkit-appearance: none) or (-moz-appearance: none) {
    input[type='checkbox'],
    input[type='radio'] {
      -webkit-appearance: none;
      -moz-appearance: none;
      outline: none;
      display: inline-block;
      vertical-align: top;
      position: relative;
      margin: 0;
      cursor: pointer;
      border: 1px solid #ffffff;
      transition: background .3s, border-color .3s, box-shadow .2s;
      &:after {
        content: '';
        display: block;
        left: 0;
        top: 0;
        position: absolute;
        transition: transform 0.3s, ease, opacity 0.2s;
      }
      &:checked {
        background: #F9B235 ;
        background: rgba(249, 178, 53, 0.05);
        box-shadow: inset 0px 0px 26px rgba(249, 178, 53, 0.34);
        backdrop-filter: blur(65.0602px);
      }
      &:disabled {
        background: #ffffff;
        border: 2px solid #BDBDBD;
        cursor: not-allowed;
        &:checked {

        }
        & + label {
          cursor: not-allowed;
          opacity: 0.6;
        }
      }
      &:not(.switch) {
        &:after {
          opacity: 0;
        }
        &:checked {
          &:after {
            opacity: 1;
          }
        }
      }
      & + label {
        font-size: 14px;
        line-height: 21px;
        display: inline-block;
        vertical-align: top;
        cursor: pointer;
        margin-left: 4px;
      }
    }
    input[type='checkbox'] {
      &:not(.switch) {
        border-radius: 4px;
        &:after {
          width: 5px;
          height: 9px;
          border: 2px solid #ffffff;
          border-top: 0;
          border-left: 0;
          left: 50%;
          top: 50%;
          position: absolute;
          transform: translate(-50%,-50%) rotate(43deg);
        }
      }
      &.switch {
        width: 44px;
        height: 24px;
        border-radius: 11px;
        background: #E0E0E0;
        &:after {
          left: 3px;
          top: 2px;
          border-radius: 50%;
          width: 18px;
          height: 18px;
          background: #ffffff;
          transform: translateX(0);
        }
        &:checked {
          background:#1F3470;
          &:after{
            transform: translateX(17px);
          }
        }
      }
      &.checkbox{
        width: 24px;
        height: 24px;
        position: relative;
        border: 2px solid #F9B235;
        &:checked {
          border: 2px solid #F9B235;
        }
      }
    }
    input[type='radio'] {
      border-radius: 50%;
      border: 2px solid #828282;
      width: 24px;
      height: 24px;
      position: relative;
      &:after {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: #F9B235;
        opacity: 0;
        position: absolute;
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
      }
      &:checked {
        background: #ffffff;
        border: 2px solid #F9B235;
      }
    }
  }
  .form-element {
    display: flex;
    align-items: center;
    &-block {
      display: flex;
      flex-direction: column;
    }
    label.form-element-block {
      margin-left: 16px;
    }
    .checkbox {
      &:disabled  {
        opacity: 0.5!important;
        & + label {
          opacity: 0.5!important;
        }
      }
    }
  }

  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #030A1D;
  z-index: 999999;
  padding-left: 56px;
  padding-right: 56px;
  @media(max-width: 550px) {
    padding-left: 3px;
    padding-right: 3px;
  }
  &-content {
    background: #010715;
    border: 1.5px solid #F9B235;
    box-shadow: 0px 4px 28px rgba(0, 0, 0, 0.58);
    border-radius: 40px;
    padding: 40px;
    max-width: 730px;
    width: 100%;
    @media(max-width: 550px) {
      padding: 20px;
    }
    .text {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1.5px solid rgba(255, 255, 255, 0.25);
      padding-bottom: 32px;
      @media(max-width: 550px) {
        flex-direction: column;
        align-items: center;
      }
      p {
        font-weight: 500;
        font-size: 40px;
        line-height: 140%;
        color: #FFFFFF;
        @include md {
          font-size: 32px;
          line-height: 140%;
        }
        @media(max-width: 550px) {
          font-size: 24px;
          line-height: 140%;
        }
      }
      .buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media(max-width: 550px) {
          margin-top: 32px;
          width: 100%;
          justify-content: center;
        }
        &-no,&-yes {
          background: rgba(255, 255, 255, 0.01);
          opacity: 0.6;
          border: 1.5px solid #FFFFFF;
          box-shadow: inset 0px 0px 45px rgba(255, 255, 255, 0.08);
          backdrop-filter: blur(46px);
          border-radius: 67.5px;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: #FFFFFF;
          width: fit-content;
          padding: 16px 24px;
          display: flex;
          justify-content: center;
          @media(max-width: 550px) {
            max-width: 130px;
            width: 100%;
          }
          &:hover {
            border: 1.5px solid #F9B235;
            cursor: pointer;
            font-weight: 700;
            opacity: 1;
          }
        }
        &-no {
          margin-right: 24px;
        }
      }
    }
    .form-element {
      margin-top: 32px;
      .checkbox {
        flex-shrink: 0;
      }
      label {
        margin-left: 24px!important;
      }
      span {
        font-weight: 500;
        font-size: 18px;
        line-height: 160%;
        color: #FFFFFF;
        @include md {
          font-size: 16px;
          line-height: 160%;
        }
        @media(max-width: 550px) {
          font-size: 12px;
          line-height: 160%;
        }
        a {
          color: #F9B235;
          text-decoration: underline;
          @include md {
            font-size: 16px;
            line-height: 160%;
          }
          @media(max-width: 550px) {
            font-size: 12px;
            line-height: 160%;
          }
        }
      }
    }
  }
}
