.page-carousel  {
  touch-action: none;
  display: flex;
  height: 400px;
  perspective: 1000px;
  transform-style: preserve-3d;
  &-drag-container,
  &-spin-container {
    position: relative;
    display: flex;
    margin: auto;
    transform-style: preserve-3d;
    transform: rotateX(-10deg);
  }
}
.page-carousel-drag-container .page-carousel_item {
  transform-style: preserve-3d;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  //box-shadow: 0 4px 8px rgba(0,0,0,0.2), 0 10px 16px rgba(0,0,0,0.1);
  border-radius: 26px;
}
.page-carousel-drag-container img {
  width: 100%;
  height: 100%;
  transition: filter 0.3s;
  border-radius: 26px;
}

.page-carousel-drag-container p {
  line-height: 1;
  font-size: 50px;
  font-weight: bold;
  text-align: center;
  font-family: Serif;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  color: #337AB7;
  font-family: Verdana, sans-serif;
}
.page-carousel-ground {
  width: 900px;
  height: 900px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
}

@keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
@keyframes spinRevert {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
