*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	font-size: 14px;
}

html,
body {
	min-height: 100%;
}

body {
	font-size: 1rem;
	color: #fff;
	position: relative;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

.wrapper {
	margin: 0 45px;
	width: calc(100% - 90px);
}

.hidden {
	visibility: hidden;
}

.visible {
	visibility: visible;
}
