.preloader {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 999999;
  opacity: 1;
  transform: opacity 1s linear;
  background: linear-gradient(208.33deg, rgba(3, 10, 29, 0.98) 2.8%, rgba(3, 10, 29, 0.95) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  @media (min-width: 1500px) {
    min-height: 1056px;
  }
  .first-page__text {
    font-weight: 200;
    font-size: 48px;
    @include sm {
      top: 40%;
      width: auto;
    }
  }
  .first-page__join .join-circle.active,
  .first-page__join .join-circle{
    background: rgba(255, 255, 255, 0.01);
    box-shadow: inset 0px 0px 45px rgba(249, 178, 53, 0.25);
    backdrop-filter: blur(8px);
    border: 1.5px solid #F9B235;
  }
  .first-page__join {
    cursor: pointer;
  }
  h2,
  p {
    letter-spacing: 0.1em;
  }
  &__line {
    margin-top: 5em;
    animation: mover 0.4s infinite  alternate;
    @media(max-height: 800px) {
      height: 50px;
      margin-top: 3em;
    }
    @include sm {
      margin-top: 3em;
    }
  }
}

@keyframes mover {
  0% { transform: translateY(5px); }
  100% { transform: translateY(-5px); }
}
