.three-page {
  position: relative;
  padding-bottom: 60px;
  padding-top: 130px;
  overflow: hidden;
  .page-carousel {
    @include md {
      position: relative;
      bottom: -50px;
    }
    @include sm {
      left: calc(100% - 350px);
      margin-bottom: 200px;
    }
    @media (max-width: 680px) {
      left: 100px ;
    }
    @media (max-width: 600px) {
      left: 120px;
    }
    @media (max-width: 480px) {
      left: 90px;
      bottom: -200px;
    }
    @media (max-width: 390px) {
      left: 60px;
    }
    &-drag-container {
      @include lg {
        transform: rotateX(-10deg) scale(0.85);
      }
      @media (max-width: 1024px) {
        transform: rotateX(-10deg) scale(0.8);
      }
      @include md {
        transform: rotateX(-10deg) scale(0.75);
      }
      @media (max-width: 880px) {
        transform: rotateX(-10deg) scale(0.6);
      }
      @include sm {
        transform: rotate(-90deg);
      }
      @media (max-width: 680px) {
        transform: rotate(-90deg) scale(0.9);
      }
      @media (max-width: 520px) {
        transform: rotate(-90deg) scale(0.7);
      }
      @media (max-width: 390px) {
        transform: rotate(-90deg) scale(0.6);
      }
    }
  }
  @include md {
    h2 {
      margin-top: 120px;
      margin-bottom: 0;
    }
  }
  @include sm {
    h2 {
      margin-top: 0;
      margin-bottom: 120px;
    }
  }
  @media (max-width: 550px) {
    h2 {
      margin-bottom: 60px;
    }
  }
  @media (max-width: 480px) {
    h2 {
      margin-bottom: 0;
      position: relative;
      bottom: -80px;
    }
  }
  @media (max-width: 390px) {
    h2 {
      bottom: -100px;
    }
  }
  @media (max-width: 360px) {
    h2 {
      bottom: -180px;
    }
  }
  &_bg {
    position: absolute;
    left: 0;
    top: 40px;
    height: 100%;
    width: 100%;
    object-fit: contain;
    z-index: 0;
    transform: scale(1.2);
    @media (max-width: 680px) {
      left: -60px;
    }
    @media (max-width: 600px) {
      left: -40px;
      transform: scale(1.5);
    }
    @media (max-width: 360px) {
      top: -20px;
    }
  }
  .wrapper {
    z-index: 5;
    position: relative;
    max-width: 998px;
    margin: 0 auto;
    @include md {
      max-width: 540px;
    }
    @media (max-width: 480px) {
      height: 1000px;
    }
    @media (max-width: 360px) {
      height: 1100px;
    }
  }
  &-footer {
    @include md {
      flex-direction: column;
      justify-content: center;
    }
    @media (max-width: 480px) {
      margin-top: 240px;
    }
  }
  &-footer__item {
    max-width: 371px;
    @include md {
      max-width: none;
      margin-right: 0;
      margin-bottom: 24px;
    }
    &:first-child {
      max-width: 300px;
      @include md {
        max-width: none;
      }
    }
    &:last-child {
      max-width: 300px;
      @include md {
        max-width: none;
      }
    }
    &__icon {
      width: 54px;
      height: 54px;
      border-radius: 100%;
      border: 1.5px solid #FFFFFF;
      flex-shrink: 0;
      z-index: 10;
      svg {
        width: 24px;
        height: 24px;
      }
      &:hover {
        background: rgba(249, 178, 53, 0.08);
        border: 1px solid #F9B235;
        box-shadow: inset 0 0 40px rgba(249, 178, 53, 0.16);
        backdrop-filter: blur(40px);
        cursor: pointer;
      }
    }
    &__text {
      opacity: 0.7;
    }
  }

}



