.sidebars-menu_left {
  left: 0;
}
.menu_left {
  &_item {
    cursor: pointer;
    margin-bottom: 18px;
    &:last-child {
      margin-bottom: 0;
    }
    &__divider-wrapper {
      margin-right: 18px;
    }
    &__divider {
      width: 27px;
      height: 2px;
      background-color: #fff;
      opacity: 0.7;
      &:last-child {
        transition: width 0.3s ease-out;
        width: 0;
        height: 0;
        margin-top: 5px;
      }
    }
    &_text {
      text-transform: uppercase;
      font-weight: 500;
    }
    &_icon {
      display: block;
    }
    .menu_left_item_text {
      display: none;
    }
  }
  &_item.navigation_links_active {
    .menu_left_item__divider {
      background-color: #f9b235;
      &:last-child {
        width: 18px;
        height: 2px;
      }
    }
    .menu_left_item_text {
      display: block;
      color: #f9b235;
    }
    .menu_left_item_icon {
      display: none;
    }
  }
}
