@mixin musicHover {
    @content;
    cursor: pointer;
    border-color: #F9B235;
    width: 180px;
    border-width: 1px;
    .music-player_text {
        transition: opacity 0.3s ease-in, width 0.25s linear;
        opacity: 1;
        width: auto;
    }
}

.music-player {
    position: absolute;
    left: 0;
    bottom: 50px;
    border: 0.75px solid rgba(255, 255, 255, 0.4);
    border-radius: 38px;
    width: 100px;
    height: 60px;
    z-index: 1000;
    padding: 20px;
    transition: width 0.25s linear;
    &:hover {
        @include musicHover;
    }
    &_icon {
        min-width: 20px;
    }
    &_text {
        opacity: 0;
        width: 0;
    }
    &_btn {
        min-width: 25px;
        &-item {
            display: none;
        }
        &-item.active {
            display: block;
        }
    }
}
