.button {
  padding: 18px 26px;
  border: 1px solid;
  border-radius: 67px;
  font-weight: 700;
  text-transform: uppercase;
  backdrop-filter: blur(46px);
  cursor: pointer;
  min-width: 340px;
  @include sm {
    padding: 12px 18px;
    font-size: 10px;
    min-width: 240px;
  }
  svg {
    margin-right: 12px;
  }
  &__light {
    color: #FFFFFF;
    box-shadow: inset 0 0 45px rgba(255, 255, 255, 0.08);
    background: rgba(255, 255, 255, 0.01);
    svg {
      path {
        fill: #FFFFFF;
      }
    }
    &:hover {
      color: #F9B235;
      background: rgba(249, 178, 53, 0.08);
      box-shadow: inset 0 0 21px rgba(249, 178, 53, 0.15);
      svg {
        path {
          fill: #F9B235;
        }
      }
    }
  }
  &__main {
    color: #F9B235;
    background: rgba(249, 178, 53, 0.08);
    box-shadow: inset 0 0 21px rgba(249, 178, 53, 0.15);
    svg {
      path {
        fill: #F9B235;
      }
    }
  }
}
