.scroll-down .header {
  transform: translate(0, -100%);
}

.scroll-up .header {
  transform: none;
}

.header {
  font-weight: 500;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: transform 0.4s;
  padding-top: 45px;
  min-height: 195px;
  @media (max-width: 768px) {
    min-height: 55px;
    padding-top: 25px;
  }
  &__logo {
    display: none;
    max-width: 410px;
  }
  &_left {
    max-width: 190px;
    position: absolute;
    left: 0;
    .music-player {
      display: none;
    }
    @media (max-width: 1120px) {
      display: none;
    }
    @include sm {
      display: block;
      .music-player {
        display: flex;
        border: none;
        width: 60px;
        padding: 0;
        position: relative;
        top: -15px;
        margin-left: 0;
        p {
          display: none;
        }
      }
      .header_left__logo {
        display: none;
      }
    }
  }
  &_right {
    position: absolute;
    right: 0;
    @media (max-width: 1120px) {
      width: 100%;
      left: 0;
      justify-content: space-between;
    }
    @include sm {
      width: auto;
      left: auto;
    }
  }
  &_right_lang {
    margin-right: 27px;
    @media (max-width: 1120px) {
    }
    &__item {
      &:hover {
        cursor: pointer;
      }
    }
    &__item.active {
      color: #F9B235;
    }
  }
  .divider_vertical {
    margin: 0 12px;
  }
}

.trigger_menu {
  z-index: 200;
  position: relative;
  height: auto;
  width: 24px;
  &_item {
    width: 24px;
    height: 2px;
    background-color: #fff;
    margin-bottom: 6px;
    transition: width 0.245s ease-in-out, background-color 0.1s step-end, transform 0.2s ease-in-out;
    &:last-child {
      width: 18px;
      margin-bottom: 0;
    }
  }
  &:hover {
    cursor: pointer;
    .trigger_menu_item:first-child {
      background-color: #F9B235;
    }
    .trigger_menu_item:last-child {
      background-color: #F9B235;
      width: 24px;
    }
  }
}
