:root{
  --menu-speed: 0.75s;
}
body.menu-open {
  .wrapper {
    @include sm {
      margin: 0 40px;
      width: calc(100% - 80px);
    }
  }
  .hamburger-menu {
    visibility: visible;
    .wrapper {
      @media (max-width: 1120px) {
        margin-left: 0;
        margin-right: 0;
      }
      @include sm {
        width: 100%;
      }
    }
    .sidebars-menu {
      @include sm {
        position: initial;
        bottom: 0;
        top: auto;
        width: 100%;
      }
      @media (min-height: 600px) and (max-height: 1000px) and (max-width: 768px) {
        bottom: -50px;
        position: relative;
      }
      @media (min-height: 750px) and (max-height: 1000px) and (max-width: 768px) {
        bottom: -100px;
        position: relative;
      }
      &_right {
        justify-content: space-between;
        @include sm {
          flex-direction: row;
        }
      }
    }
    .menu_right_item {
      @include sm {
        margin: 0 12px 0 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  .hamburger-menu > div{
    transform: scale(1);
    transition-duration: var(--menu-speed);
  }
  .hamburger-menu > div > div{
    opacity: 1;
    transition: opacity 0.4s ease;
  }
  .hamburger-menu-slider-wrapper {
    @include sm {
      flex-direction: column-reverse;
      //justify-content: space-between;
    }
  }
  .header {
    z-index: 110000;
    .wrapper {
      @media (max-width: 1120px) {
        margin-left: 0;
        margin-right: 0;
      }
    }
    @media (max-width: 1120px) and (max-height: 540px) {
      min-height: 100px;
    }
    @include sm {
      padding-top: 15px;
    }

  }
  .first-page__logo {
    display: none;
  }
  .music-player {
    display: none;
  }
  .header__logo {
    display: block;
    @media (max-width: 1120px) {
      margin-top: 120px;
    }
    @media (max-width: 1120px) and (max-height: 540px) {
      display: none;
    }
    @include sm {
      width: 230px;
      margin-top: 40px;
      height: auto;
      min-height: 0;
    }
  }
  .header_left {
    @media (max-width: 1120px) {
      display: block;
    }
    @include sm {
      top: 0;
    }
    .music-player {
      display: flex;
      position: initial;
      @include musicHover;
      @include sm{
        border: none;
        width: 60px;
        padding: 0;
        p {
          display: none;
        }
      }
    }
    &__logo {
      display: none;
    }
  }
  .header_right {
    @media (max-width: 1120px) {
      width: auto;
      left: auto;
    }
  }
  .trigger_menu {
    height: 24px;
  }
  .trigger_menu_item {
    width: 24px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
    &:first-child {
      transform: rotate(45deg);
    }
    &:last-child {
      transform: rotate(-45deg);
    }
  }
}
