.hamburger-menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000;
  &_item {
    width: 100%;
    height: 100%;
    &_text {
      font-size: 34px;
      font-weight: 200;
      color: #FFFFFF;
      opacity: 0.25;
      padding: 0 30px;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      text-align: center;
      @include sm {
        font-size: 18px;
        padding: 0 15px;
      }
    }
    &__divider {
      background: #F9B235;
      height: 3px;
      visibility: hidden;
      @include sm {
        height: 2px;
      }
      &:first-child {
        width: 50px;
        margin-bottom: 10px;
        @include sm {
          width: 26px;
        }
      }
      &:last-child {
        width: 38px;
        @include sm {
          width: 20px;
        }
      }
    }
    &__divider-left-wrapper{
      .hamburger-menu_item__divider:first-child {
        margin-left: 25px;
        @include sm {
          margin-left: 7px;
        }
      }
    }
    &__divider-right-wrapper {
      align-items: flex-end;
      .hamburger-menu_item__divider:first-child {
        margin-right: 25px;
        @include sm {
          margin-right: 7px;
        }
      }
    }
  }
  &_item.navigation_links_active,
  &_item.swiper-slide-active{
    .hamburger-menu_item_text {
      font-weight: 700;
      color: #F9B235;
      opacity: 1;
    }
    .hamburger-menu_item__divider {
      visibility: visible;
    }
  }
  &-footer {
    margin-top: -60px;
    width: 100%;
    @include sm {
      flex-direction: column;
      .button {
        margin-bottom: 17px;
        width: 100%;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

.hamburger-menu-slider-container {
  width: 100%;
  height: 100%;
  .button {
    margin-right: 15px;
    &:last-child {
      margin-right: 0;
    }
  }
  @include sm {
    padding: 0 20px;
  }
}
.hamburger-menu-slider {
  z-index: 111000;
  position: relative;
  width: 100%;
  height: 100%;
  max-height: 400px;
  overflow: hidden;
  @include lg {
    height: 350px;
  }
  @include sm {
    height: 170px;
  }
  &-wrapper {
    width: 100%;
    height: calc(100% - 60px);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    @include sm {
      height: calc(100% - 130px);
    }
    .sidebars-menu {
      position: absolute;
      left: 0;
      right: 100%;
      @include lg {
        margin-top: 70px;
      }
      @include sm {
        margin-top: 35px;
      }
      .wrapper {
        width: 100%;
        margin: 0;
      }
      &_right {
        align-items: flex-start;
      }
    }
  }
  &-btn-wrapper {
    max-height: 500px;
    height: 100%;
    padding-top: 50px;
    @include lg {
      padding-top: 100px;
    }
    @include sm {
      max-height: 210px;
      padding-top: 0;
      margin-top: 160px;
    }
  }
  &-button-next,
  &-button-prev {
   svg {
     padding: 5px;
     cursor: pointer;
     min-width: 22px;
     min-height: 14px;
     height: 24px;
     width: 32px;
     @include sm {
       width: 16px;
       height: 9px;
       padding: 3px;
     }
   }
  }
  .swiper-slide-next,
  .swiper-slide-prev{
    .hamburger-menu_item_text {
      opacity: 0.5;
    }
  }
}

.hamburger-menu > div{
  background: linear-gradient(208.33deg, rgba(7, 18, 39, 0.95) 2.8%, rgba(7, 18, 39, 0.85) 100%);
  border-radius: 50%;
  backdrop-filter: blur(15px);
  width: 200vw;
  height: 200vw;
  display: flex;
  flex: none;
  align-items: center;
  justify-content: center;
  transform: scale(0);
  transition: all 0.4s ease;
  min-width: 1024px;
  min-height: 1024px;
}
.hamburger-menu > div > div{
  text-align: center;
  max-width: 90vw;
  max-height: 100vh;
  opacity: 0;
  transition: opacity 0.4s ease;
}
