.menu-wrapper {
  position: absolute;
  width: 100%;
  left: 0;
  height: calc((100vh + 280px) - 100%);
  top: calc(100vh + 280px);
  @media (min-width: 1500px) {
    top: 1300px;
    height: calc(100% - 1300px);
  }
}

.sidebars-menu-wrapper_left {
  position: absolute;
  left: 0;
  height: 100%;
  @media(max-width: 992px) {
    display: none;
  }
}
.sidebars-menu-wrapper_right {
  position: absolute;
  right: 0;
  height: 100%;
  @media(max-width: 992px) {
    display: none;
  }
}
.sidebars-menu {
  position: sticky;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  z-index: 20000;
  &_left,
  &_right {
    width: fit-content;
  }
}
