.sidebars-menu_right {
  right: 0;
  margin-left: auto;
}
.menu_right {
  &_item {
    margin-bottom: 18px;
  }
  &_item__fill {
    &:hover {
      circle {
        stroke: #F9B235;
        opacity: 1;
      }
      path {
        fill: #F9B235;
      }
    }
  }

  &_item__stroke {
    &:hover {
      circle {
        stroke: #F9B235;
        opacity: 1;
      }
      path {
        stroke: #F9B235;
      }
    }
  }
}
