@font-face {
  font-family: Ukraine;
  src: local("Ukraine-Thin"), url(../fonts/e-Ukraine-Thin.otf) format("opentype");
  font-weight: 100;
  font-style: normal;
  font-display: swap
}
@font-face {
  font-family: Ukraine;
  src: local("Ukraine-Light"), url(../fonts/e-Ukraine-Light.otf) format("opentype");
