  font-style: normal;
  font-display: swap
}
@font-face {
  font-family: Ukraine;
  src: local("Ukraine-Medium"), url(../fonts/e-Ukraine-Medium.otf) format("opentype");
  font-weight: 500;
  font-style: normal;
  font-display: swap
}
@font-face {
  font-family: Ukraine;
  src: local("Ukraine-Regular"), url(../fonts/e-Ukraine-Regular.otf) format("opentype");
