.blue-bg {
  background: #030A1D;

}
.two-page {
  background: #030A1D;
  padding: 190px 0;
  position: relative;
  background: linear-gradient(180deg, rgba(3, 10, 29, 0) 0%, #030A1D 15.6%);
  @media (max-width: 700px) {
    padding: 0 0;
  }
  &-link {
    position: absolute;
    bottom: 50px;
    right: 0;
    display: flex;
    align-items: center;
    @media(max-width: 1200px) {
      max-width: 330px;
      justify-content: flex-end;
      display: flex;
      text-align: end;
    }
    @media (max-width: 550px) {
      bottom: -100px;
    }
    span,a {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #FFFFFF;
      @media (max-width: 550px) {
        font-size: 12px;
        line-height: 14px;
      }
    }
    a {
      color: #F9B235;
    }
    .line {
      width: 35px;
      height: 1px;
      background: #F9B235;
      margin-left: 24px;
    }
  }
  &__bg {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    @media (max-width: 700px) {
      object-position: top;
    }
  }
  .container {
    max-width: 1120px;
    width: 100%;
    margin: 0 auto;
    padding: 0 24px;
    @include xs {
      padding: 0 20px;
    }
  }
  &-content {
    display: flex;
    justify-content: space-between;
    transform: translateY(15px);
    @media (max-width: 700px) {
      transform: translateY(0px);
    }
    *  {
      z-index: 10;
    }
    @media (max-width: 700px) {
     flex-direction: column;
      align-items: center;
    }
  }
  &-left {
    align-self: end;
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: -20px;
    @media (max-width: 760px) {
      margin-right: 20px;
    }
    @media (max-width: 700px) {
      margin-right: 0px;
      align-items: center;
      order: 2;
      margin-top: 60px;
      margin-bottom: 0;
    }
    span {
      max-width: 330px;
      width: 100%;
    }
   p,span {
     text-align: right;
   }
  }
  &-center {
    max-width: 156px;
    width: 100%;
    @media (max-width: 700px) {
      order: 1;
      max-width: 160px;
    }
    @include xs {
      margin-top: 160px
    }
    p {
      font-weight: 500;
      line-height: 105%;
      color: #FFFFFF;
      text-transform: uppercase;
      text-align: center;
      font-size: 14px;

      color: #F9B235;
      line-height: 153.92%;
    }
    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 21px;
      color: #FFFFFF;
      opacity: 0.7;
    }
    .text {
      p {
        margin-bottom: 12px;
      }
    }
  }
  &-right {
    align-self: start;
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    //margin-top: 20px;
    @media (max-width: 760px) {
      margin-left: 20px;
    }
    @media (max-width: 700px) {
      margin-left: 0px;
      align-items: center;
      order: 3;
      margin-top: 24px;
    }
    p,span {
      text-align: left;
    }
    span {
      max-width: 400px;
      width: 100%;
      @media (max-width: 700px) {
        order: 3;
      }
    }
    p {
      @media (max-width: 700px) {
        order: 2;
      }
    }
    .buttons {
      width: 100%;
      @media (max-width: 700px) {
        order: 1;
        margin-bottom: 40px;
      }
      &-rarible, &-opensea {
        display: flex;
        align-items: center;
        border: 0.75px solid #FFFFFF;
        border-radius: 67.5px;
        padding: 16px 0px;
        max-width: 280px;
        width: 100%;
        justify-content: center;
        margin-top: 16px;
        &:hover  {
          cursor: pointer;
          border: 0.75px solid #F9B235;
          background: rgba(249, 178, 53, 0.08);
          border: 1px solid #F9B235;
          box-shadow: inset 0px 0px 40px rgba(249, 178, 53, 0.16);
          backdrop-filter: blur(40px);
          span {
            color: #F9B235;
          }
        }
        span {
          margin-top: 0;
          margin-left: 14px;
          font-size: 14px;
          line-height: 17px;
          color: #FFFFFF;
          text-transform: uppercase;
          font-weight: 600;
          opacity: 1;
          width: fit-content;
        }
      }
    }
  }

  &-left,&-right {
    width: calc(50% - 125px);
    @media (max-width: 700px) {
      width: 100%;
      max-width: 330px;
      align-self: center;
      align-items: flex-start;
    }
    p {
      font-weight: 500;
      font-size: 40px;
      line-height: 48px;
      color: #FFFFFF;
      @include sm {
        font-size: 32px;
        line-height: 38px;
      }
      @include xs {
        font-size: 24px;
        line-height: 29px;
      }
    }
    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: #FFFFFF;
      opacity: 0.7;
      margin-top: 12px;
      display: block;
      @media (max-width: 700px) {
        text-align: start;
        margin-top: 16px;
      }
    }
  }
}
.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 16px;
  position: relative;
  @media (max-width: 700px) {
    margin-top: 24px;
  }
  p {
    min-inline-size: 10em;
    writing-mode: vertical-lr;
    text-orientation: upright;
    text-align: start;
    color: #FFFFFF;
    font-weight: 700;
    &:first-child {
      margin-top: 10px;
      margin-right: 15px;
    }
    &:last-child {
      margin-top: 15px;
      margin-left: 15px;
    }
  }

  &-circle {
    border-radius: 100%;
    border: 1px solid #ffffff;
    transform: rotate(45deg);
    width: 142px;
    height: 142px;
    position: relative;
    overflow: hidden;

    &.active-left {
      &:after {
        content: '';
        background: linear-gradient(273.47deg,rgba(249,178,53,.6) -1.38%,rgba(249,178,53,.328755) 60.97%,rgba(249,178,53,0) 106.55%);
        opacity: .25;
        left: 0%;
        position: absolute;
        transform: rotate(135deg) translateX(10px) translateY(-22px);
        width: 70px;
        height: 140px;
      }
    }
    &.active-right {
      &:after {
        content: '';
        background: linear-gradient(273.47deg,rgba(249,178,53,.6) -1.38%,rgba(249,178,53,.328755) 60.97%,rgba(249,178,53,0) 106.55%);
        opacity: .25;
        right: 0%;
        position: absolute;
        transform: rotate(315deg) translateX(10px) translateY(-22px);
        width: 70px;
        height: 140px;
      }
    }
  }

  &-line {
    display: flex;
    p {
      font-family: IBM,sans-serif;
      font-size: 24px;
      line-height: 105%;
    }
    &-left  {
      height: 110px;
      width: 1px;
      background: #FFFFFF;
      margin-right: 4px;
      transition: height 0.3s;
      &.active {
        height: 160px;
        background:  #F9B235;
      }
    }
    &-right  {
      height: 110px;
      width: 1px;
      background: #FFFFFF;
      margin-left: 4px;
      transition: height 0.3s;
      &.active {
        height: 160px;
        background:  #F9B235;
      }
    }
  }

  &-number {
    //position: absolute;
    top: 50%;
    left: 50%;
    //transform: translate(-50%,-50%) rotate(-45deg);
  }

  &-left {
    //position: absolute;
    left: 20px;
    top: 50%;
    //transform: translate(-50%,-50%) rotate(-45deg);
  }

  &-right {
    //position: absolute;
    right: 20px;
    top: 50%;
    //transform: translate(-50%,-50%);
  }

  &-input {
    display: flex;
    justify-content: space-around;
    align-self: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) rotate(-45deg);
    width: 100%;
  }

  &-click__left {
    width: 70px;
    left: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    background: #FFFFFF;
    opacity: 0.6;
    bottom: 300px;
  }
  &-click__right {
    width: 70px;
    right: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    background: #FFFFFF;
    opacity: 0.6;
    bottom: 300px;
  }


  &.active-left {
    .counter-circle {
      border-bottom: 1px solid #F9B235;
      border-left: 1px solid #F9B235;
    }
    .counter-line__left {
      color: #F9B235;
    }
  }

  &.active-right {
    .counter-circle {
      border-top: 1px solid #F9B235;
      border-right: 1px solid #F9B235;
    }
    .counter-line__right {
      color: #F9B235;
    }
  }
}
